import cloudbase from "@cloudbase/js-sdk";
const app = cloudbase.init({    //ID
    env: "hello-cloudbase-9g3pfrfm2651f00e"
});

const db = app.database();

export function onSubmit() {
    const user=db.collection("user").get()
    console.log(user)
    return user;
}

export function infoadd(data) {
    let infoadd=db.collection('infoList')
    console.log(data)
    infoadd.add(data)
    return data;

}
export function infoset(data) {
    let infoadd=db.collection('infoList')
    console.log(":"+data)
    infoadd.doc(data.name).update(data)
    return infoadd;
}


export function getInfo() {
    let infoList=db.collection('infoList').get()
    console.log(infoList)
    return infoList;
}

export function infoDel(row) {
    let infoDel=db.collection('user')
    infoDel.doc(row.id).remove()
    return infoadd;
}